.ProjectLink {
  margin-bottom: 1.7em;
  transform: translateY(20px) scale(0.97);
  opacity: 0;
  transition: 0.5s;
  transition-timing-function: ease-out;

  display: grid;
  grid-template-rows: min-content;
  grid-template-columns: 35px auto;
  column-gap: 15px;
  grid-template-areas: 
    "id name"
    "id desc";

  &__name {
    font-size: 30pt;
    font-family: "ObjectSans";
    font-weight: bold;
    font-style: italic;
    color: #E3E3E3;
    grid-area: name;
  }

  &__shortDesc {
    font-size: 10pt;
    font-family: "ObjectSans";
    font-style: italic;
    color: #D3D3D3;
    margin-top: 0.4em;
    line-height: 1.2em;
    grid-area: desc;
  }

  &__wrapper {
    width: 100%;
    margin-top: 0.4em;
    transition: 0.3s;
    grid-area: id;
    justify-self: center;
  }
  
  &__line {
    border-top: solid #999999 1px;
    height: 1px;
    opacity: 0;
    
    transform: translateX(-100%);
    transition: 0.2s;
  }

  &__id {
    font-size: 35pt;
    font-family: "HelveticaBoldOutline";
    color: #AAAAAA;
    text-align: center;
    line-height: 1em;
    margin-top: 0.1em;

    transform: translateX(-120%);
    transition: 0.4s;
  }

}

// modifier classes for ProjectLink animations
.fade-in { 
  opacity: 1; 
  transform: translateY(0) scale(1); 
}

.slide { 
  opacity: 1;
  transform: translateX(0); 
}